import React from "react";
import cl from "./NavBar.module.css";
import MyLink from "../UI/link/MyLink";

const NavBar = (props) => {
  return (
    <div className={cl.nav}>
      <MyLink to="/profile"></MyLink>
      <MyLink to="/messages"></MyLink>
      <MyLink to="/users"></MyLink>
      <MyLink to="/news"></MyLink>
      <MyLink to="/music"></MyLink>
      <MyLink to="/settings"> </MyLink>

      {props.auth.isAuth ? (
        // props.auth.login ? (
        <div className={cl.logAva}>
          {props.auth.login}
          <button onClick={props.logOut}></button>s
        </div>
      ) : (
        // ) : (
        //   "Me"
        // )
        <MyLink to="/login" className={cl.login}>
          "Login"
        </MyLink>
      )}
    </div>
  );
};

export default NavBar;
